"use client"

import ErrorPageMain from "@/app/components/error_page_main"
import {baseUrlAtom} from "@/app/state"
import { useEffect } from "react"
import {useAtom} from 'jotai'

export default function ErrorPage() {  

    const [baseUrl, setBaseUrl] = useAtom(baseUrlAtom)

    useEffect(() => {
        setBaseUrl("http://evans-energy-b9ba239f1ce7.herokuapp.com/")
    }, [])
    return <ErrorPageMain baseUrl={baseUrl} />
}